import React, {useState} from 'react'; 
import './App.css'; 

function App() { 
	let [text, setText] = useState('')
	let [inputNum, setInputNum] = useState('')
	let [numArr, setNumArr] = useState([])
	console.log(numArr);
	
	let calcInput = (input) => {
		if (input === '=') {
			inputNum = parseInt(inputNum)
			numArr.push(inputNum)
			console.log(numArr);
			
			let eq = calc(numArr);
			console.log(eq);
			
			setText(eq);
			setNumArr([]);
			setInputNum(eq)
			return 
		}

		if(input === '+' ||  input === '-' || input === '*' || input === '/' ){
			console.log(text);
			
			setText(text + input);
			inputNum = parseInt(inputNum)
			console.log(inputNum);
			numArr.push(inputNum)
			numArr.push(input)
			setNumArr(numArr)
			setInputNum('')
			return;
		}
		setText(text + input)
		input = input
		setInputNum(inputNum + input)

	}

	let calc = (numArr) => {
		
		let equel = '';
		let sign;
		numArr.forEach(e => {
			if (typeof(equel) === 'number') {  
				if (typeof(e) === 'number') {
					if (sign === '+') { equel = equel + e }
					if (sign === '-') { equel = equel - e }
					if (sign === '*') { equel = equel * e }
					if (sign === '/') { equel = equel / e }			
				}else{
					sign = e;
				}		
			}
			else { equel = e} 
		});
		return equel
	}
	
	return (
		<div className="App">
			<div>
				HELLO CALCLATE
			</div>
			<div id="big_table">
			<div>
				{text}
			</div>
			<br />
				<table id="table">
					<tbody onClick={e => calcInput(e.target.innerText)}>

				
					<tr>
						<td>1</td>
						<td>2</td>
						<td>3</td>
						<td>+</td>
					</tr>
					<tr>
						<td>4</td>
						<td>5</td>
						<td>6</td>
						<td>-</td>
					</tr>
					<tr>
						<td>7</td>
						<td>8</td>
						<td>9</td>
						<td>*</td>
					</tr>
					<tr>
						<td>=</td>
						<td>0</td>
						<td>.</td>
						<td>/</td>
					</tr>
					</tbody>
				</table>
			</div>
		</div>
	); 
} export default App;
